@font-face {
  font-family: "Amaranth";
  src: url("../../static/fonts/Amaranth-Regular.ttf");
}

@font-face {
  font-family: "Amaranth Italic";
  src: url("../../static/fonts/Amaranth-Italic.ttf");
}

@font-face {
  font-family: "Girl";
  src: url("../../static/fonts/TheGirlNextDoor.ttf");
}

.main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: auto;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  height: 100%;
}

.main img {
  max-width: 300px;
  padding: 15px;
}

.main h1 {
  font-family: "Girl";
  font-size: 6rem;
  color: #3C474B;
  margin-left: 15px;
}